.nav-card{
    text-align: center;
    border: black 2px solid;
    border-radius: 5px;
    padding: 20px;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    height: 400px;
    position: relative;
}
.absolute-centered{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 5px;

}
.absolute-centered:hover{
    background-color:rgba(0,0,0,0.6);
    color: white;
}
#map-nav-card{
    position: relative;
}
#map-nav-card::before{
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    opacity: 0.3;
    background-image: url(/static/media/map.fcc2a1c9.png);
    background-position: center;
    background-size: cover;
    bacgkround-repeat: no-repeat;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}
#map-nav-card:hover{
    border-color: #34a0a4;
}
#map-nav-card:hover::before{
    opacity: 0.8;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

#activity-nav-card{
    position: relative
}
#activity-nav-card:hover{
    border-color: #34a0a4;
}
#activity-nav-card::before{
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    opacity: 0.3;
    background-image: url(/static/media/activity.f3a6065b.png);
    background-position: center;
    background-size: cover;
    bacgkround-repeat: no-repeat;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}
#activity-nav-card:hover::before{
    opacity: 0.8;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    border-color: #E8E1D9;
}

